<template>
        <div class="quick-chat-container">
            <div style="display: flex; align-items: flex-end; justify-content:space-between;">
                <h4 class="header-title">{{chat_room.name}} (owner: {{chat_room.owner.username}})<small></small></h4>
                <b-button @click="onClose" size="sm" style="padding:10px">Close</b-button>
            </div>
            <MessageDisplay :messages="chat_room.lastMessages" />
            <div class="container-message-manager">
                <div class="message-text-box">
                    <b-form-input v-model="input_message" required></b-form-input>
                </div>
                <b-button @click="sendMessage">Send</b-button>
            </div>
        </div>
</template>

<script>

    import MessageDisplay from './MessageDisplay.vue'
    import {CREATE_MESSAGE} from './../../constants/graphgl.js'

    export default {
        name: 'chat',
        data: function(){
            return {
                input_message:'',
            }               
        },
        props: {
            chat_room: {
                require:true
            }
        },
        components: {
            MessageDisplay,
        },
        methods: {
            onClose: function(){
                this.$emit("onClose");
            },
            sendMessage: async function(){
                const text = this.input_message
                this.input_message = ''
                await this.$apollo.mutate({
                    mutation: CREATE_MESSAGE,
                    variables: {
                        text: text
                    }
                })
            }
        },
    }
</script>

