<template>
  <div>
    <div :style="styleOfRoomItem(room)">
      <span v-if="isUserRoom(room)">
        <b-button pill size="sm" variant="success" style="margin-right: 10px" @click="onEdit" >
          Edit
        </b-button>
        <b-button pill size="sm" variant="warning" style="margin-right: 10px" @click="onDelete">
          Del
        </b-button>
      </span>
      <span>
        {{ room.name }}<small>(owner:<i style="color: green">{{ room.owner.username }})</i></small>
        <b-button pill size="sm" variant="primary" style="margin-left: 10px" @click="onJoin">
          Join
        </b-button>
      </span>
    </div>
  </div>
</template>

<script>
export default {
  name: "RoomItem",
  props: {
    room: {},
  },
  methods: {
    onJoin: async function () {
      this.$emit("onJoin");
    },
    onEdit: async function () {
      this.$emit("onEdit");
    },
    onDelete: async function () {
      this.$emit("onDelete");
    },
    isUserRoom: function (room) {
      return room.owner.id === JSON.parse(localStorage.getItem("user")).id;
    },
    styleOfRoomItem: function (room) {
      return this.isUserRoom(room)
        ? "display: flex; justify-content:space-between;"
        : "text-align:right;";
    },
  },
};
</script>