<template>
    <div class="container-message-display">
        <div v-for="(message, index) in messages" :key="index" class="message-container" >
            <MyMessage v-if="isMyMessage(message)" 
                :message="message" />
            <OtherMessage v-else 
                :message="message"/>
        </div>
    </div>
</template>

<script>
    import MyMessage from './MyMessage.vue';
    import OtherMessage from './OtherMessage.vue';
    export default {
        name: "MessageDisplay",
        components:{
            MyMessage,
            OtherMessage
        },
        data() {
            return {
                updateScroll: true,
                lastMessage: null,
                loading: false,
            }
        },
        props: {
            messages: {
                require: true,
            },
        },
        methods: {
            isMyMessage(message){
                return message.author.id === JSON.parse(localStorage.getItem('user')).id
            }
        },
    }
</script>

<style scope>
    .quick-chat-container .container-message-display {
        flex: 1;
        overflow-y: scroll;
        overflow-x: hidden;
        display: flex;
        flex-direction: column;
        padding-bottom: 10px;
        max-height: 100%;
    }

    .message-container {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
    }


</style>