<template>
  <div>
    <ModalWithoutInput ref="modalWithoutInput"/>
    
    <b-modal
      ref="modalWithInput"
      id="modalWithInput"
      :title="modalTitle"
      @show="resetModalForm"
      @hidden="resetModalForm"
      @ok="OkModal"
    >
      <form ref="form" @submit.stop.prevent="handleSubmit">
        <b-form-group
          :label="modalText"
          label-for="name-input"
          invalid-feedback="Name is required"
        >
          <b-form-input
            id="name-input"
            v-model="input_text_in_modal"
            :state="formTextState"
            required
          ></b-form-input>
        </b-form-group>
      </form>
    </b-modal>


    <div style="width: 400px">
      
      <b-container>
        <b-row>
          <b-col>
            <b-form-checkbox v-model="onlyUsersRooms" style="text-align: left">only Users room</b-form-checkbox>
          </b-col>
          <b-col>
            <b-button class="float-right" v-on:click="CreateRoom" size="sm">Create a new room</b-button>
          </b-col> 
        </b-row>
      </b-container>
      <div class="list-group-wrapper">
        <ul class="list-group" id="infinite-list">
          <li
            class="list-group-item"
            v-for="room in filteredRooms"
            v-bind:key="room.id"
          >
            <RoomItem 
              :room="room" 
              @onJoin="JoinToRoom(room.id)" 
              @onDelete="DeleteRoom(room)"  
              @onEdit="EditRoom(room)"
            />
          </li>
        </ul>
      </div>
      <br>
    </div>
  </div>
</template>

<script>
import {
  CREATE_ROOM,
  UPDATE_ROOM,
  DELETE_ROOM,
} from "../constants/graphgl.js";
import RoomItem from "./RoomItem.vue";
import ModalWithoutInput from "./my_lib/ModalWithoutInput.vue";

export default {
  name: "RoomList",
  components: {
    RoomItem,
    ModalWithoutInput,
  },

  data: function () {
    return {
      onlyUsersRooms: false,
      
      modalTitle: "",
      modalText: "",
      input_text_in_modal: "",
      formTextState: null,

      OkModal: () => console.log("hi"),
    };
  },
  props: {
    rooms: {
      require: true,
    }
  },
  computed: {
    filteredRooms: function () {
      if (this.onlyUsersRooms) {
        try {
          const id = JSON.parse(localStorage.getItem("user")).id;
          return this.rooms.filter((room) => room.owner.id === id);
        } catch (e) {
          console.log(e);
          return this.rooms;
        }
      } else {
        return this.rooms;
      }
    },
  },

  methods: {
    resetModalForm: function () {
      this.input_text_in_modal = "";
      this.formTextState = null;
    },
    CreateRoom: async function () {
      this.modalTitle = `Create a new room`;
      this.modalText = "Enter new name:";
      this.OkModal = async (bvModalEvt) => {
        bvModalEvt.preventDefault();
        this.formTextState = this.input_text_in_modal !== "";
        if (this.formTextState) {
          const result = await this.$apollo.mutate({
            mutation: CREATE_ROOM,
            variables: {
              roomname: this.input_text_in_modal,
            },
          });
          console.log(`Created room ${result.data}`);
          this.$nextTick(() => {
            this.$bvModal.hide("modalWithInput");
          });
        }
      };
      this.$bvModal.show("modalWithInput");
    },
    EditRoom: async function (room) {
      this.modalTitle = `Edit room "${room.name}"`;
      this.modalText = "Enter new name:";
      this.OkModal = async (bvModalEvt) => {
        bvModalEvt.preventDefault();
        this.formTextState = this.input_text_in_modal !== "";
        if (this.formTextState) {
          const result = await this.$apollo.mutate({
            mutation: UPDATE_ROOM,
            variables: {
              id: room.id,
              new_name: this.input_text_in_modal,
            },
          });
          console.log(`Edited room ${result}`);
          this.$nextTick(() => {
            this.$bvModal.hide("modalWithInput");
          });
        }
      };
      this.$bvModal.show("modalWithInput");
    },
    DeleteRoom: async function (room) {
      const modal = this.$refs.modalWithoutInput
      modal.setTitle(`Delete room "${room.name}"`)
      modal.setText(`Are you sure?`)
      modal.setOkFunction(async () => {
        const result = await this.$apollo.mutate({
          mutation: DELETE_ROOM,
          variables: {
            id: room.id,
          },
        });
        console.log(`Deleted room ${result}`);
      })
      modal.show()
    },
    JoinToRoom: async function (id) {
      this.$emit("JoinToRoom", id);
    },
    isUserRoom: function (room) {
      return room.owner.id === JSON.parse(localStorage.getItem("user")).id;
    },
    styleOfRoomItem: function (room) {
      return this.isUserRoom(room)
        ? "display: flex; justify-content:space-between;"
        : "text-align:right;";
    },
  },
};
</script>

<style scoped>
 .list-group-wrapper {
  position: relative;
} 
.list-group {
  overflow: auto;
  border: 2px solid #dce4ec;
  border-radius: 5px;
  height: 50vh;
  min-height: 200px;
}
.list-group-item {
  margin-top: 1px;
  border-left: none;
  border-right: none;
  border-top: none;
  border-bottom: 2px solid #dce4ec;
}
</style>