<template>
    <div class="content">
      <div>
        <RoomList :rooms="rooms" @JoinToRoom="joinToRoom" />
        <Alert ref="Alert" style="margin:auto; width:75%;" />
      </div>
      <div style="display: flex;">
        <div class="chat-container">
          <Chat v-if="chat_room" :chat_room="chat_room" @onClose="onClose"/>
          <Members v-if="chat_room" :members="chat_room.members"/>
        </div>
      </div>
    </div>
</template>

<script>
import Alert from "./my_lib/Alert.vue";

import {reskip} from './../functions/reskip.js'
import RoomList from './RoomList.vue'
import Chat from './chat/Chat.vue'
import Members from './chat/Members.vue'
import {
  ROOMS, 
  JOIN_ROOM, 
  LEAVE_ROOM, 
  ME, 
  MESSAGE_CREATED,  
  SUB_ROOM_CREATED,
  SUB_ROOM_UPDATED,
  SUB_ROOM_DELETED,} from './../constants/graphgl.js'

export default {
    name: 'Rooms',
    data: function() {
      return {
        rooms : [],
        chat_room: null
      }
    },
    components: {
        RoomList,
        Chat,
        Members,
        Alert,
    },
    mounted: async function() {
      reskip(this.$apollo.queries.me, {})
      const resultMe = await this.$apollo.queries.me.refetch();
      localStorage.setItem('user', JSON.stringify(resultMe.data.me))
      const currentRoom = resultMe.data.me.currentRoom
      if(currentRoom){
        this.chat_room = currentRoom
      }
    },    

    apollo: {
      rooms: {
        query: ROOMS,
      },
      me: {
        query: ME,
        skip: true,
      },
      $subscribe: {
          messageCreated: {
              query: MESSAGE_CREATED,
              result({data}){
                  const {messageCreated} = data
                  this.chat_room.lastMessages.push(messageCreated)                        
                  this.chat_room.lastMessages.sort((a,b) => a.timestamp > b.timestamp)
                  this.$refs.Alert.showAlert(`new message in room`)
              }
          },
          roomCreated: {
            query: SUB_ROOM_CREATED,
            result({ data }) {
              const { roomCreated } = data;
              this.rooms.push(roomCreated)
              this.$refs.Alert.showAlert(`new room: ${roomCreated.name}`)
            },
          },
          roomUpdated: {
            query: SUB_ROOM_UPDATED,
            result({ data }) {
              const { roomUpdated } = data;
              const index = this.rooms.findIndex((room) => room.id === roomUpdated.id)
              if(index > -1){
                this.rooms[index] = roomUpdated
                this.$refs.Alert.showAlert(`updated room: ${roomUpdated.name}`)
              }
            },
          },
          roomDeleted: {
            query: SUB_ROOM_DELETED,
            result({ data }) {
              const { roomDeleted } = data;
              this.rooms = this.rooms.filter((room) => room.id !== roomDeleted.id)
              if(this.chat_room){
                if(roomDeleted.id === this.chat_room.id){
                  this.chat_room = null
                  this.$refs.Alert.showAlert(`YOUR ROOM IS DELETED\ndeleted room: ${roomDeleted.name}`)
                }else{
                  this.$refs.Alert.showAlert(`deleted room: ${roomDeleted.name}`)
                }
              }else{
                this.$refs.Alert.showAlert(`deleted room: ${roomDeleted.name}`)
              }
            },
          },
      },
    },

    methods: {
        joinToRoom: async function(roomId){
          await this.leaveRoom()
          const result = await this.$apollo.mutate({
            mutation: JOIN_ROOM,
            variables: {
              id: roomId,
            },
          });
          this.chat_room = result.data.joinRoom
        },
        leaveRoom: async function(){
          try{
            await this.$apollo.mutate({
              mutation: LEAVE_ROOM
            })
          }catch(e){
            console.log(e.message)
          }
        },
        onClose: async function() {
          await this.leaveRoom()
          this.chat_room = null
        },
    },
}
  
</script>

