<template>
    <div>
        <h4>Members:</h4>
      <div class="list-group-wrapper" style="width:200px;">
        <ul class="list-group" id="infinite-list">
          <li
            class="list-group-item"
            v-for="member in members"
            v-bind:key="member.id"
          >
          {{member.username}}
          </li>
        </ul>
      </div> 
      <Alert ref="Alert" style="margin:auto; width:75%;" />
    </div> 
</template> 

<script>
import Alert from '../my_lib/Alert.vue'
import {MEMBER_JOINED, MEMBER_LEFT} from './../../constants/graphgl.js'

export default {
    name: "Members",
    props: {
        members:{
          require: true,
        }
    },
    components: {
        Alert,
    },
    apollo:{
        $subscribe: {
            memberJoined:{
                query: MEMBER_JOINED,
                result({ data }) {
                    const {memberJoined} = data
                    const finded = this.members.find((m) => m.id === memberJoined.id)
                    console.log(finded)
                    if(!finded){
                      this.members.push(memberJoined)
                      this.$refs.Alert.showAlert(`Member ${memberJoined.username} joined"`)
                    }
                },
            },
            memberLeft:{
                query: MEMBER_LEFT,
                result({ data }) {
                    const {memberLeft} = data
                    this.members = this.members.filter((member) => member.id !== memberLeft.id)
                    this.$refs.Alert.showAlert(`Member ${memberLeft.username}" left`)
                },
            }
        }
    }
}
</script>

<style scoped>
.list-group {
  overflow: auto;
  border: 2px solid #dce4ec;
  border-radius: 5px;
  height: 40vh;
  min-height: 200px;
}
.list-group-item {
  margin-top: 1px;
  border-left: none;
  border-right: none;
  border-top: none;
  border-bottom: 2px solid #dce4ec;
}
</style>
